* {
margin: 0;
padding: 0;
}

html, body, #root {
  height: 100%;
}

body {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin: 0 auto;
  margin-left: 0 auto;
}

img {
  max-width: 200px;

}
.profile {
  max-width: 150px;
  display: grid;
    float: left;
    margin-right: 20px;
   
  }


form {
  width: 380px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;

}

form > * {
  padding: 7px 10px;
  margin-bottom: 7px;
  border: 1px solid #ccc;
  outline: none;

}
main {

  padding: 25px 3%;
}
main h1.main-header {
  text-align: center;
  margin-bottom: 18px;
  
}

main p {

  max-width: 50%;
  text-align: center;
  margin: 0 auto;
  line-height: 1.5;
}
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #999;
  padding: 9px 3%;
}
h3 {

  color: #fff;
}
header nav a {
  color: #fff;
 font-size: .95rem;
 text-decoration: none; 
}

header nav a {
  color: #fff;
  text-decoration: none;
}
header nav a:not(:last-child) {
  margin-right: 10px;
}

header nav a.active {
  text-decoration: underline;
}

.project-gallery, .book-gallery {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.project, .book {
  position: relative;
  overflow: hidden;
  cursor: pointer;
  width: 200px;  /* Ensure that the book div has a width */
  height: auto;  /* And it adjusts its height to maintain the image aspect ratio */

}

.project img, .book img {
  max-width: 100%;
  height: auto;
  transition: transform 0.3s ease-out;
}

.project:hover img, .book:hover img {
  transform: scale(1.1);
}

.project .overlay, .book .overlay {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
  transition: opacity 0.3s ease-out;
}

.project:hover .overlay, .book:hover .overlay {
  opacity: 1;
}

.project .overlay h3, .book .overlay h3 {
  color: #fff;
  font-size: 1.5rem;
  text-align: center;
}
.pdf-link-box {
  background-color: #f5f5f5;
  border: 2px solid #0faf4c;
  border-radius: 4px;
  display: inline-block;
  padding: 10px;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  color: #830f0f;
  text-decoration: none;
  
 
}

.pdf-link-box:hover {
  background-color: #e0e0e0;
  border-color: #999;
  color: #000;
}
.text-box {
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin: 20px 0;
}

